import Vue from 'vue'
import Router from 'vue-router'
let __import__ = (page) => _ => import('page/'+ page+ '.vue');
Vue.use(Router)
let router = new Router({
  routes: [
    {
      path: '/messages',
      name: 'messages',
      component: __import__('messages/index'),
      meta: {
        requireSignin: true
      }
    },
    {
      path: '/chat/:id',
      name: 'chat',
      component: __import__('messages/chat'),
      meta: {
        requireSignin: true
      }
    },

    {
      path: '/linkman',
      name: 'linkman',
      component: __import__('linkman/index'),
      meta: {
        tier: 0,
        requireSignin: true
      }
    },
    {
      path: '/addfriend',
      name: 'addfriend',
      component: __import__('linkman/addfriend'),
      meta: {
        tier: 1,
        requireSignin: true
      }
    },
    {
      path: '/newfriend',
      name: 'newfriend',
      component: __import__('linkman/newfriend'),
      meta: {
        tier: 2,
        requireSignin: true
      }
    },
    {
      path: '/usercard/:account',
      name: 'usercard',
      component: __import__('linkman/usercard'),
      meta: {
        tier: 2,
        requireSignin: true
      }
    },
    
    {
      path: '/',
      name: 'index',
      component: __import__('index'),
      meta: {
        to: true,
        requireSignin: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: __import__('login'),
      meta: {
        tier: 0
      },
    },
    {
      path: '/register',
      name: 'register',
      redirect: '/register/nickname',
      children: [
        {
          path: 'success',
          name: 'register_success',
          component: __import__('register/register-success'),
          meta: {
            tier: 3,
            step: 3
          }
        },
        {
          path: 'nickname',
          name: 'register_nickname',
          component: __import__('register/register-nickname'),
          meta: {
            step: 1,
            tier: 1
          }
        },
        {
          path: 'password',
          name: 'register_password',
          component: __import__('register/register-password'),
          meta: {
            step: 2,
            tier: 2
          }
        }
      ],
      component: __import__('register/index')
    },
    {
      path: '*',
      redirect: '/messages'
    }
  ]
});
// ..

export default router 



