import chatConf from '../conf/chat-conf'
import io from 'socket.io-client'
import router from '../router/index'
let socket = io(
  `ws://${chatConf.HOST}:${chatConf.PORT}`,
  {
    path: chatConf.PATH
  }
);
let socketEmit = (event, info) => {
  info = Object.assign({
    token: localStorage.getItem('_token')
  }, info);
  return new Promise((resolve, reject) => {
    socket.emit(event, info, (rs) =>  {
      let code = rs.code;
      // 无误
      if(code === 0) {
        resolve(rs);
      }else {
        if (code === 1001) { router.push({name: 'login'}) }
        reject(rs);
      }
    });
  })
}

export { socket, socketEmit }


